import _ from 'lodash'
import React from 'react'
import { findSchema, findEnum } from '../../../data/helpers';
import { Form, Input, Select, Tabs, Button, Radio } from 'antd';
import { useWorkspaceContext } from '../../../store/workspace';

const PATH = 'FieldDefWithCondition<MarkPropFieldDef,(string|null)>'
const _SCHEMA = findSchema(PATH)
const KEYS = _.keys(_SCHEMA)

const SAMPLE_VALUE = {
	"timeUnit": "month",
	"field": "date",
	"type": "ordinal",
	"axis": {"title": "Month of the year"}
}

const formItemLayout = {
	labelCol: {
		xs: { span: 24 },
		sm: { span: 6 },
	},
	wrapperCol: {
		xs: { span: 24 },
		sm: { span: 18 },
	},
};

const EncodingFieldInput = ({spec, path, value, schema, onChange}) => {
	let enums = findEnum(schema)
	const { changeSpec } = useWorkspaceContext()
	let input = <Input value={value} onChange={(e)=>changeSpec(path, _.isNumber(value) ? Number(e.target.value) : e.target.value)}/>
	if(enums.length) {
		input = (
			<Select value={value} onChange={(v)=>changeSpec(path, v)}>
				{
					enums.map(e=><Select.Option key={e} value={e}>{_.startCase(e)}</Select.Option>)
				}
			</Select>
		)
	}
	return (
		<Form.Item label={_.startCase(spec)}>
			{input}
		</Form.Item>
	)
}

const EncodingField = ({ path, label = 'Label', value = {}, onChange, schema }) => {
	const { changeSpec } = useWorkspaceContext()

	return (
		<Form {...formItemLayout}>
			<header className={`d-flex justify-content-between`} title={JSON.stringify(schema, null, 2)}>
				<h4>{_.startCase(label)}</h4>
				<Button onClick={()=>changeSpec(path, undefined)}>Remove</Button>
			</header>
			<p>{schema.description}</p>
			{/* <Form.Item>
				<Select onChange={(v)=>changeSpec(`encoding.${v}`, {})}>
					{
						schema.anyOf.map((key, i)=>(
							<Select.Option key={i} value={i}>
								{_.startCase(key.description || key.items.description)}
							</Select.Option>
						))
					}
				</Select>
			</Form.Item>
			 */
			}
			{
				schema && schema.anyOf &&
				<Radio.Group defaultValue="a" buttonStyle="solid">
					{
						_.map(schema.anyOf, (key, i)=>(
							<Radio.Button key={i} value={i}>
								{_.startCase(key.description || key.items && key.items.description || key.type)}
							</Radio.Button>
						))
					}
				</Radio.Group>
			}
			{
				_.keys(value).map((key)=>(
					<EncodingFieldInput 
						key={key} 
						path={`${path}.${key}`} 
						spec={key} 
						value={value[key]} 
						schema={_SCHEMA.properties[key]}
						onChange={onChange}/>
				))
			}
		</Form>
	)

}


const EncodingControl = ({value, onChange}) => {
	let {properties, description} = findSchema('Encoding')
	let keys = _.keys(value)
	const { changeSpec } = useWorkspaceContext()
	return (
		<div>
			{/* <Form.Item label={'Add Encoding'}>
				<Select onChange={(v)=>changeSpec(`encoding.${v}`, {})}>
					{
						_.keys(properties).map((key)=>(
							<Select.Option key={key} disabled={_.includes(keys, key)} value={key}>
								{_.startCase(key)}
							</Select.Option>
						))
					}
				</Select>
			</Form.Item> */}
			<Tabs tabPosition={'left'} type={'editable-card'}>
				{
					_.map(_.keys(properties), (key)=>(
						<Tabs.TabPane key={key} tab={_.startCase(key)}>
							<EncodingField 
								path={`encoding.${key}`} 
								key={key} 
								label={key} 
								value={value[key]} 
								onChange={onChange}
								schema={properties[key]}/>
						</Tabs.TabPane>
					))
				}
			</Tabs>
		</div>
	)
}

export default EncodingControl