import React from 'react';
import './App.css';
import { BrowserRouter as Router, Route, Switch } from "react-router-dom";
import WorkspacePage from './pages/workspace';
import ShowcasePage from './pages/showcase';
import NotFoundPage from './pages/404';
import 'antd/dist/antd.css';
import 'bootstrap/dist/css/bootstrap.min.css'
import '@fortawesome/fontawesome-free/css/all.css'
import WorkspaceContext, { useWorkspaceStore } from './store/workspace';
import { FirebaseProvider } from './services/firebase/context';
import ShowPage from './pages/show';

function App() {
  return (
    <Router>
			<FirebaseProvider>
				<WorkspaceContext.Provider value={useWorkspaceStore()}>
					<div className="App">
						<Switch>
							<Route path="/" exact component={WorkspacePage} />
							<Route path="/workspace/" exact component={WorkspacePage} />
							<Route path="/showcase/" component={ShowcasePage} />
							<Route path="/show/:id" component={ShowPage} />
							<Route component={NotFoundPage} />
						</Switch>
					</div>
				</WorkspaceContext.Provider>
			</FirebaseProvider>
    </Router>
  );
}

export default App;
