import _ from 'lodash'
import SCHEMA from './schema.json'
import DEFAULT_SPEC from './empty.json'
const _SCHEMA = SCHEMA

const OPERANDS = [
	'#/definitions/SelectionNot',
	'#/definitions/SelectionOperand',
	'#/definitions/LogicalOperand<Predicate>',
	'#/definitions/LogicalNot<Predicate>'
]

export function getDefaultSpec() {
	return {...DEFAULT_SPEC}
}

export function getDefaultLayer() {
	return {...DEFAULT_SPEC}.spec.layer[0];
}

export function splitPath(path) {
	return _.split(path, '.')
}

export function findJsonSchema(value) {
	if(value.anyOf) {
		return _.map(value.anyOf, ({$ref})=>{
			let path = _.replace($ref, /#\//g, '')
			path = _.replace(path, /\//, '.')
			return _.get(_SCHEMA, path)
		})
	}
	return []
}

export function constructJsonSpec(value) {
	if(_.isArray(value)) {
		return _.map(value, constructJsonSpec)
	} else if(value instanceof Object) {
		const { $ref, ...rest } = value
		let ref = {}
		if($ref && !_.includes(OPERANDS, $ref)) {
			let path = _.replace($ref, /#\//g, '')
			path = _.replace(path, /\//, '.')
			ref = _.get(_SCHEMA, path)
			if(ref.$ref && !_.includes(OPERANDS, $ref)) {
				ref = {
					$ref: ref.$ref,
					...ref,
					...constructJsonSpec(ref)
				}
			}
		}
		let res = {
			...ref,
			...rest
		} 
		return _.mapValues(res, constructJsonSpec)
	} else {
		return value
	}
}

export function findSchema(path) {
	let schema = _.get(_SCHEMA.definitions, path);
	return constructJsonSpec(schema)
}

export function findEnum(schema = {}) {
	let allEnum = []
	if(schema.enum) {
		allEnum.push(...schema.enum)
	} else if(schema.anyOf) {
		schema.anyOf.forEach((s)=>allEnum.push(...findEnum(s)))
	}
	return allEnum
}

