import firebase from 'firebase'

const config = {
	apiKey: "AIzaSyDQYxDmv_YumWyn_k4KZUet4FBtJjVZCIo",
	authDomain: "chartmanpoc.firebaseapp.com",
	databaseURL: "https://chartmanpoc.firebaseio.com",
	projectId: "chartmanpoc",
	storageBucket: "chartmanpoc.appspot.com",
	messagingSenderId: "209038415542",
	appId: "1:209038415542:web:851c4710b039e1f5"
}

const initialize = () => {
	if(!firebase.apps.length) {
		firebase.initializeApp(config)
	}
}

const createBase = (collection, value) => {
	initialize()
	return firebase
	.firestore()
	.collection(collection)
	.add(value)
	.then(data => data.id)
	.then(id => ({ ...value, id }))
}

const saveBase = (collection, id, value) => {
	initialize()
	return firebase
	.firestore()
	.collection(collection)
	.doc(id)
	.set(value)
}


const getBase = (collection, id) => {
	initialize()
	return firebase
	.firestore()
	.collection(collection)
	.doc(id)
	.get()
	.then(doc=>doc.data() && { ...doc.data(), id: doc.data().id })
}

const saveOrCreateBase = (collection, id, value) => {
	return getBase(collection, id)
	.then((data) => {
		if(data) {
			return saveBase(collection, id, value)
		} else {
			return createBase(collection, value)
		}
	})
}

const queryBase = (collection, userId, where) => {
	initialize()
	let db = firebase
	.firestore()
	.collection(collection)
	.where('user_id', '==', userId)

	for (let key in where) {
		db.where(key, '==', where[key])
	}

	return db
	.get()
	.then(doc=>doc.data() && { ...doc.data(), id: doc.data().id })
}

const snapshotBase = (collection, userId, fn) => {
	initialize()
	return firebase
	.firestore()
	.collection(collection)
	.where('user_id', '==', userId)
	.onSnapshot((snapshot) => {
		let res = []
		snapshot.forEach((doc)=> {
			let data = doc.data()
			if(data) res.push({ ...data, id: doc.id })
		})
		fn(res)
	})
}

const saveNewChart = (tab, userId) => {
	initialize()
	return firebase
	.firestore()
	.collection('charts')
	.add({
		...tab,
		user_id: userId
	})
	.then((data)=>data.id)
}

const saveEditedChart = (tab, userId) => {
	initialize()
	return firebase
	.firestore()
	.collection('charts')
	.doc(tab.uuid)
	.set({
		...tab,
		user_id: userId
	})
}

const getChartCollection = (userId = '') => {
	initialize()
	return firebase
	.firestore()
	.collection("charts")
	.where('user_id', '==', userId)
	.get()
	.then((res) => {
		const data = []
		res.forEach(d=>{
			data.push({
				...d.data(),
				uuid: d.id
			})
		})
		return data
	})
}

const getFolders = (userId = '') => {
	initialize()
	return firebase
	.firestore()
	.collection("folders")
	.where('user_id', '==', userId)
	.get()
	.then((res) => {
		const data = []
		res.forEach(d=>{
			data.push({
				...d.data(),
				uuid: d.id
			})
		})
		return data
	})
}

const getUserById = (id) => {
	initialize()
	return firebase
	.firestore()
	.collection("users")
	.doc(id)
	.get()
	.then((user)=>{
		let data = user.data()
		let id = user.id
		if (!data) throw new Error('User not found')
		return {
			...data,
			id
		}
	})
}

const getUserByName = (name) => {
	initialize()
	return firebase
	.firestore()
	.collection("users")
	.where('name', '==', name)
	.get()
	.then((users)=>{
		let data, id
		users.forEach((user)=>{
			data = user.data()
			id = user.id
		})
		if (id) {
			return {
				...data,
				id
			}
		}
		return undefined
	})
}

const createUser = (name) => {
	initialize()
	return firebase
	.firestore()
	.collection("users")
	.add({name})
	.then(res=>({ name, id: res.id}))
}

const createFolder = (userId, folderName) => {
	initialize()
	return firebase
	.firestore()
	.collection("folders")
	.add({name: folderName, user_id: userId})
	.then(res=>({ name: folderName, user_id: userId, id: res.id}))
}

const saveCollection = (userId, collection) => {
	initialize()
	if (!collection.id) {
		return createFolder(userId, collection.name);
	}
	return firebase
	.firestore()
	.collection('collection')
	.doc(collection.id)
	.set({
		...collection,
		id: collection.id
	})
}

const saveWorkspace = (userId, workspace) => {
	return firebase
	.firestore()
	.collection('workspace')
	.doc(userId)
	.set(workspace)
}

export {
	config,
	initialize,
	firebase,
	saveNewChart,
	saveEditedChart,
	getChartCollection,
	getUserById,
	getUserByName,
	createUser,
	saveCollection,
	saveWorkspace,
	saveBase,
	getBase,
	createBase,
	snapshotBase,
	queryBase,
	saveOrCreateBase
}