import _ from 'lodash'
import React, { useState, useEffect, useCallback } from 'react'
import { findSchema } from '../../../data/helpers';
import { Radio, Divider, Input, Button } from 'antd';
import { useWorkspaceContext } from '../../../store/workspace';
import Code from '../code';

const URLData = ({defaultUrl, onChange}) => {
	const [url, setUrl] = useState(defaultUrl)
	const [data, setData] = useState('')
	const tryUrl = 'https://vega.github.io/vega-lite/data/cars.json'
	
	const fetchData = useCallback(
		() => {
			fetch(url)
			.then(response=>response.text())
			.then(setData)
			.catch((reason)=>setData(reason.message))
		},
		[url]
	)

	return (
		<div>
			<Divider orientation="right">URL</Divider>
			<Input.Search
				size="small"
				addonBefore="GET" 
				value={url} 
				onChange={e=>setUrl(e.target.value)}
				onSearch={fetchData}
				enterButton={'Fetch'}
				/>
			{
				!defaultUrl &&
				<p onClick={()=>setUrl(tryUrl)}>
					Try this: &nbsp;{tryUrl}
				</p>
			}
			{
				!_.isEmpty(url) &&
				<Button onClick={()=>onChange(url)} type="primary" size="small">Confirm URL as data</Button>
			}
			<p></p>
			<Code
				height={500}>
				{data}
			</Code>
		</div>
	)
}

const ValuesData = ({values, onDataChanged}) => {
	const [format, setFormat] = useState('json')
	useEffect(() => {
		return () => {
			try {
				JSON.parse(values)
				setFormat('json')
			} catch {
				setFormat('csv')
			}
		};
	}, [values])
	return (
		<div>
			<Divider orientation="right">Values</Divider>
			<Code
				height={500} 
				format={format} 
				onChange={onDataChanged}>
				{(typeof values) === 'string' ? values : JSON.stringify(values, null, 2)}
			</Code>
		</div>
	)
}

const Data = ({
	value = {}, 
	config = {}, 
	onChange = () => {}
}) => {
	
	const {getActiveTab, setData} = useWorkspaceContext()
	const tab = getActiveTab()
	const [type, setType] = useState(tab.data.type)

	return (
		<div>
			<p>Data can be a URL or Inline values of either CSV or JSON</p>
			<Radio.Group onChange={(e)=>setType(e.target.value)} value={type}>
        <Radio value={'url'}>URL</Radio>
        <Radio value={'values'}>Values</Radio>
      </Radio.Group>
			{
				type === 'url' &&
				<URLData defaultUrl={tab.data.value} onChange={(value)=>setData('url', value)}/>
			}
			{
				type === 'values' &&
				<ValuesData values={tab.data.value} onDataChanged={(value)=>setData('values', value)}/>
			}
		</div>
	)
}
const DataSchema = {
	title: 'Data',
	id: 'data',
	path: 'data',
	generator: (type, value) => {
		return {
			[type]: value
		}
	},
	Component: Data,
	config: (store) => {
		return { }
	},
	$schema: findSchema('DataSource')
}

export default Data
export {
	DataSchema
}