import React, { memo } from 'react'
import Highlight, {defaultProps} from 'prism-react-renderer';
import MonacoEditor from 'react-monaco-editor';

const CodeOld = ({children, format, style}) => {
	return (
		<div style={style}>
			<Highlight {...defaultProps} code={children} language={format}>
				{({ className, style, tokens, getLineProps, getTokenProps }) => (
					<pre className={className} style={style}>
						{tokens.map((line, i) => (
							<div {...getLineProps({ line, key: i })}>
								{line.map((token, key) => (
									<span {...getTokenProps({ token, key })} />
								))}
							</div>
						))}
					</pre>
				)}
			</Highlight>
		</div>
	)
}

const Code = memo(({children, format = 'json', height = 500, style, onChange}) => {
	const options = {
		selectOnLineNumbers: true
	}
	return (
		<div style={style}>
			<MonacoEditor
        width="100%"
        height={height}
        language={format}
        theme="vs-dark"
        value={children}
        options={options}
				onChange={onChange}
      />
		</div>
	)
})

export default Code