import _ from 'lodash'
import React from 'react'
import { Tabs, Form, Input, Select } from 'antd';
import { findSchema } from '../../../../data/helpers';
import EncodingControl from '../../../controls/encoding-field';
import Schema from './schema';

const Option = Select.Option

const controls = {
	"mark": "bar",
	"encoding": {
    "x": {
      "timeUnit": "month",
      "field": "date",
      "type": "ordinal",
			"axis": {"title": "Month of the year"}
    },
    "y": {
      "aggregate": "count",
      "type": "quantitative"
    },
    "color": {
      "field": "weather",
      "type": "nominal",
      "scale": {
        "domain": ["sun","fog","drizzle","rain","snow"],
        "range": ["#e7ba52","#c7c7c7","#aec7e8","#1f77b4","#9467bd"]
      },
      "legend": {"title": "Weather type"}
    }
	}
}

const FieldControl = ({label, value}) => {
	return (
		<Form.Item
			label={_.capitalize(label)}>
			<Input
				placeholder="Select field"
				value={value.field}
			/>
		</Form.Item>
	)
}

const AggregateControl = ({label, value, onChange}) => {
	const schema = findSchema('AggregateOp')
	return (
		<Form.Item
			label={_.capitalize(label)}>
			<Select value={value.aggregate} onChange={(v)=>onChange('aggregate', v)}>
				{
					_.map(schema.enum, (opt)=>(
						<Option value={opt}>{_.capitalize(opt)}</Option>
					))
				}
			</Select>
		</Form.Item>
	)
}


const MarkControl = ({value, onChange}) => {
	const schema = findSchema('Mark')
	return (
		<Form>
			<desc>{schema.description}</desc>
			<Form.Item label={'Type'}>
				<Select value={value} onChange={onChange}>
					{
						schema.enum.map((type)=><Option value={type} key={type}>{_.capitalize(type)}</Option>)
					}
				</Select>
			</Form.Item>
		</Form>
	)
}

const ControlSelector = ({specKey, value, onChange}) => {
	switch(specKey) {
		case 'encoding':
			return <EncodingControl value={value} onChange={(p, v)=>onChange(`encoding.${p}`, v)}/>
		case 'mark':
			return <MarkControl value={value} onChange={(v)=>onChange('mark', v)}/>
		default:
			return null
	}
}

const ControlGroup = ({tab, onChange, store = {}}) => {
	const { spec } = tab
	return (
		<Tabs tabPosition={'left'} defaultActiveKey={'layer'}>
			{/*
				_.chain(tab.spec)
				.toPairs()
				.map(([key, value]) => (
					<Tabs.TabPane tab={_.capitalize(key)} key={key}>
						<ControlSelector 
							specKey={key} 
							value={value} 
							onChange={onChange}/>
					</Tabs.TabPane>
				))
				.value()
			*/}
			{
				_.chain(Schema)
				.map((schemaItem) => {
					const { Component, id, title, path, config } = schemaItem
					const value = _.get(spec, path)
					return (
						<Tabs.TabPane tab={title} key={id}>
							<Component 
								value={value}
								config={config(store)}
								onChange={(value)=>onChange(path, value)}
								/>
						</Tabs.TabPane>
					)
				})
				.value()
			}
		</Tabs>
	)
}

export default ControlGroup