import React, { useState, useEffect } from 'react'
import VegaLite from 'react-vega-lite';
import barSpec from '../data/samples/bar.json'
import { Layout } from 'antd';
import SEO from '../components/seo.js';
import { useParams } from '../helpers/router.js';
import { useFirebaseContext } from '../services/firebase/context.js';

const ShowPage = () => {
	const {id} = useParams()
	const { getBase } = useFirebaseContext()
	const [ spec, setSpec ] = useState()
	useEffect(() => {
		getBase('charts', id)
		.then((chart)=>chart && setSpec(chart.spec))
	}, [id])
  return (
		<Layout>
			{
				spec &&
				<VegaLite spec={spec} data={spec.data}/>
			}
			{
				!spec && <p>Chart must be saved before embed.</p>
			}
		</Layout>
	)
}

export default ShowPage