import _ from 'lodash'
import React, { useEffect, useState, useCallback, memo } from 'react'
import { Input, Button, Dropdown, Menu, Modal, Popconfirm, Select, Form } from 'antd'
import { DATA_SOURCE, useWorkspaceContext, switchTab } from '../../../store/workspace';
import Code from '../code';
import { useFirebaseContext, useFirebaseSnapshot } from '../../../services/firebase/context';

const InputGroup = Input.Group

const DataModal = ({show, onCancel, onOk}) => {

	const {getActiveTab, setData} = useWorkspaceContext()
	const tab = getActiveTab()
	
	const [value, setValue] = useState("")
	const [format, setFormat] = useState("json")
	useEffect(()=>{
		if(tab.data.type === DATA_SOURCE.Url) {
			setFormat(_.last(tab.data.value.split('.')) || 'json')
			fetch(tab.data.value)
			.then(response=>response.text())
			.then(setValue)
		} else {
			setValue(_.isString(tab.data.value) ? tab.data.value : JSON.stringify(tab.data.value, null, 2))
		}
	}, [tab.data.type, tab.data.value])

	const onDataChanged = useCallback(
		(value) => {
			if(tab.data.type === 'values') {
				setData('values', value)
			}
		},
		[setData, tab]
	)

	return (
		<Modal
			width={600}
			title="Data"
			visible={show}
			onCancel={onCancel}
			onOk={onOk}
		>
			<Code 
				height={500} 
				format={format} 
				onChange={onDataChanged}>
				{value}
			</Code>
		</Modal>
	)
}

const DataMenu = ({onSelectionChanged}) => {
	return (
		<Menu onClick={(e)=>onSelectionChanged(e.key)}>
			{
				_.keys(DATA_SOURCE).map((source)=>(
					<Menu.Item key={source}>{source}</Menu.Item>
				))
			}
		</Menu>
	);
}

const NameModal = ({show, toggleShow}) => {
	const { getActiveTab, setTabProp, switchTab, store } = useWorkspaceContext()
	const { saveOrCreateBase, saveBase } = useFirebaseContext()
	const folders = useFirebaseSnapshot('collection', [], store.user.id)
	const tab = getActiveTab()

	const [ error, setError ] = useState()
	const [ name, setName ] = useState(tab.name)
	const [ folder, setFolder ] = useState({})

	const saveChart = useCallback(
		() => {
			saveOrCreateBase('charts', tab.id, { ...tab, name })
			.then((chart) => {
				const cObj = {name: chart.name, id: chart.id}
				const charts = (folder.charts || []).map((c)=>{
					if(c.id !== chart.id) return c
					return cObj
				})
				if (!_.includes(_.map(charts, 'id'), chart.id)) {
					charts.push(cObj)
				}

				saveBase('collection', folder.id, {
					...folder,
					charts
				})
				setTabProp(cObj)
				switchTab(cObj.id)
			})
			.catch((e)=>setError(e.message || e))
		},
		[tab, name, folder, folders]
	)
	
	useEffect(() => {
		if (tab.id) {
			_.each(folders, (f) => {
				if(_.includes(_.map(f.charts, 'id'), tab.id)) {
					setFolder(f)
					return
				}
			})
		}
		setFolder(_.first(folders) || {})
	}, [folders, tab])

	return (
		<Modal visible={show} onCancel={()=>toggleShow(false)} onOk={saveChart} okText="Save Chart">
			<Form.Item label="Chart name">
				<Input value={name} onChange={(e)=>setName(e.target.value)}/>
			</Form.Item>
			<Form.Item label="Chart collection">
				<Select value={folder.id} onChange={(id)=>setFolder(_.find(folders, {id}))} style={{width: '100%'}}>
				{
					_.map(folders, (f) => (
						<Select.Option key={f.id}>{f.name}</Select.Option>
					))
				}
			</Select>
			</Form.Item>
			{error}
		</Modal>
	)
}

