import _ from 'lodash'
import React, { useCallback, memo, useState, useEffect } from 'react'
import { findSchema, findEnum } from '../../../data/helpers';
import { Select, Form, Divider, Dropdown, Button, Menu, Input, Collapse } from 'antd';
import { useWorkspaceContext } from '../../../store/workspace';
import { formItemLayout } from '../../../helpers/form';
import {SketchPicker} from 'react-color'
import * as vega from 'vega'
import csvjson from 'csvjson'
const SCHEMES = [
	"accent",
	"category10",
	"category20",
	"category20b",
	"category20c",
	"dark2",
	"paired",
	"pastel1",
	"pastel2",
	"set1",
	"set2",
	"set3",
	"tableau10",
	"tableau20"
]
const DropdownMenu = memo(({
	label = '',
	options = [],
	value = '',
	onMenuItemClicked
}) => {
	const onItemClicked = useCallback((e)=> {
		onMenuItemClicked && onMenuItemClicked(e.key)
	}, [onMenuItemClicked])
	const schemeMenu = (
		<Menu style={{maxHeight: 200, overflowY: 'auto', boxShadow: '0px -1px 10px lightgrey'}}>
			{
				_.map(options, (key)=>(
					<Menu.Item 
						key={key}
						onClick={onItemClicked}
						>{key}
					</Menu.Item>
				))
			}
		</Menu>
	)
	return (
		<Dropdown.Button overlay={schemeMenu} icon={value} size="small">
			{label}
		</Dropdown.Button>
	)
})

const LayerEncoding = memo(({
	layerIndex = 0,
	encoding = '',
	value = {}
}) => {
	const { changeSpec, activeTab : {dataKeys = []} } = useWorkspaceContext()
	const types = findEnum(findSchema('StandardType'))
	const aggregates = findEnum(findSchema('AggregateOp'))
	const rootPath = `spec.layer[${layerIndex}].encoding.${encoding}`
	return (
		<Form.Item label={_.capitalize(encoding)}>
			{
				value.field &&
				<DropdownMenu
					label={'Field'}
					options={dataKeys}
					value={value.field}
					onMenuItemClicked={(v)=>changeSpec(`${rootPath}.field`, v)}
				/>
			}
			{
				value.type &&
				<DropdownMenu
					label={'Type'}
					options={types}
					value={value.type}
					onMenuItemClicked={(v)=>changeSpec(`${rootPath}.type`, v)}
				/>
			}
			{
				value.aggregate &&
				<DropdownMenu
					label={'Aggregate'}
					options={aggregates}
					value={value.aggregate}
					onMenuItemClicked={(v)=>changeSpec(`${rootPath}.aggregate`, v)}
				/>
			}
			{
				value.scale && value.scale.scheme &&
				<DropdownMenu
					label={'Scheme'}
					options={SCHEMES}
					value={value.scale.scheme}
					onMenuItemClicked={(v)=>changeSpec(`${rootPath}.scale.scheme`, v)}
				/>
			}
			{
				value.axis &&
				<Input 
					size="small" 
					addonBefore="Axis" 
					value={_.get(value, 'axis.title')}
					onChange={(e)=>changeSpec(`${rootPath}.axis.title`, e.target.value)}
					addonAfter={
						<Select 
							size="small" 
							value={value.axis.orient} 
							style={{ width: 80 }} 
							onChange={(v)=>changeSpec(`${rootPath}.axis.orient`, v)}
							>
							{
								_.map({
									y: ['left', 'right'],
									x: ['bottom', 'top']
								}[encoding], (v)=>(
									<Select.Option key={v} value={v}>{_.capitalize(v)}</Select.Option>
								))
							}
						</Select>
					}
					/>
			}
			{
				value.legend &&
				<Input 
					size="small" 
					addonBefore="Legend" 
					value={_.get(value, 'legend.title')}
					onChange={(e)=>changeSpec(`${rootPath}.legend.title`, e.target.value)}
					/>
			}
			{
				encoding === 'color' && value.value &&
				<Input.Group compact>
					<Button size="small">Value</Button>
					<Dropdown overlay={<SketchPicker color={value.value} onChange={(value)=>changeSpec(`${rootPath}.value`, value.hex)}/>}>
						<Button size="small" style={{backgroundColor:value.value}}>{value.value}</Button>
					</Dropdown>
				</Input.Group>
			}
		</Form.Item>
	)
})

const LayerMark = memo(({
	layerIndex = 0,
	value,
	onChange
}) => {
	const { changeSpec } = useWorkspaceContext()
	const schema = findSchema('Mark')
	const type = _.isObject(value) ? value.type : value
	return (
		<Form.Item label={'Mark'}>
			<Select value={type} onChange={(v)=>changeSpec(`spec.layer[${layerIndex}].mark${_.isObject(value)?'.type':''}`,v)} size="small">
				{
					schema.enum.map((type)=><Select.Option value={type} key={type}>{_.capitalize(type)}</Select.Option>)
				}
			</Select>
		</Form.Item>
	)
})

const LayerItem = memo(({
	value: {
		mark = 'bar',
		encoding = {}
	} = { },
	index = 0
}) => {
	const encodings = _.keys(encoding)
	const schema = findSchema('Encoding')

	const menu = (
		<Menu>
			{
				_.keys(schema.properties)
				.map((type) => (
					<Menu.Item key={type} disabled={_.includes(encodings, type)}>{_.startCase(type)}</Menu.Item>
					)
				)
			}
		</Menu>
	)

	return (
		<Form {...formItemLayout}>
			<LayerMark value={mark} layerIndex={index}/>
			{
				_.map(encodings, (key)=><LayerEncoding layerIndex={index} key={key} encoding={key} value={encoding[key]}/>)
			}
			<Dropdown overlay={menu} overlayStyle={{maxHeight: 200, overflowY: 'auto', boxShadow: '0px -1px 10px lightgrey'}}>
				<Button type={'primary'} size="small">+ Add Encoding</Button>
			</Dropdown>
		</Form>
	)
})

const Layer = memo(({
	value = [], 
	config: {
		marks = [],
		encodings = []
	} = {}, 
	onChange = () => {}
}) => {
	const { changeSpec, getActiveTab, setDataKeys } = useWorkspaceContext()
	const [ activeLayer, setActiveLayer ] = useState(0)
	const tab = getActiveTab()
	const url = (tab.spec && tab.spec.data.url) || ''
	const addLayer = useCallback(
		() => {
			const newLayer = {..._.last(tab.spec.spec.layer)}
			newLayer.mark = newLayer.mark === 'bar' ? 'line' : 'bar'
			changeSpec('spec.layer', [...tab.spec.spec.layer, newLayer])
			setActiveLayer(activeLayer*1 + 1)
		},
		[tab],
	)

	useEffect(() => {
		if (_.isEmpty(tab.dataKeys) && url) {
			fetch(url)
			.then(response=>response.text())
			.then((text)=> {
				let value = text
				try {
					value = JSON.parse(value)
				} catch (e) {
					value = csvjson.toObject(value)
				}
				return value
			})
			.then(data => _.isArray(data) ? _.keys(data[0]) : [])
			.then(keys => {
				console.log('keys', keys)
				setDataKeys(keys)
			})
			.catch(console.error)
		}
	}, [url])

	return (
		<div>
			<p>
				Layer multiple encodings to create composite chart.
			</p>
			<Collapse accordion bordered={false} activeKey={[String(activeLayer)]} onChange={setActiveLayer}>
				{
					_.map(tab.spec.spec.layer, (layer, index) => {
						return (
							<Collapse.Panel header={`Layer ${index+1} : ${_.capitalize((layer.mark && layer.mark.type) || layer.mark)}`} key={index}>
								<LayerItem value={layer} index={index}/>
							</Collapse.Panel>
						)
					})
				}
			</Collapse>
			<Divider orientation="right"><Button type={'primary'} size="small" onClick={addLayer}>+ Add Layer</Button></Divider>
		</div>
	)
})

const LayerSchema = {
	title: 'Layer',
	id: 'layer',
	path: 'spec.layer',
	generator: (mark = 'bar', encodings = {}) => {
		return [{
			mark,
			encodings
		}]
	},
	Component: Layer,
	config: (store) => {
		return { }
	},
	$schema: findSchema('LayerRef')
}

export default Layer
export {
	LayerSchema
}