import _ from 'lodash'
import React, { useCallback, useEffect, useState, memo } from 'react'
import { useWorkspaceContext } from '../../store/workspace';
import { Layout, Menu, Icon, Tabs } from 'antd';
import Editor from './editor';
import { useFirebaseContext, useFirebaseSnapshot } from '../../services/firebase/context';

const { SubMenu } = Menu;
const { Content, Sider } = Layout;

const WorkspaceFolder = memo(({onSelectItem}) => {
	const {store: { user = {}}} = useWorkspaceContext()
	const folders = useFirebaseSnapshot('collection', [], user.id)

	return (
		<Menu
			mode="inline"
			style={{height: '100%'}}>
			{
				_.map(folders, (folder) => (
					<SubMenu
						key={folder.id}
						title={
							<span title={folder.description}>
								<Icon type="folder" />
								{`${folder.name} (${_.size(folder.charts)})`}
							</span>
						}>
						{
							!_.isEmpty(folder.charts) && 
							_.map(folder.charts, (chart) => (
								<Menu.Item key={chart.id} onClick={()=>onSelectItem(chart.id, {}, chart.name)}>{chart.name}</Menu.Item>
							))
						}
					</SubMenu>
				))
			}
		</Menu>
	)
})


const WorkspaceFolderOld = memo(({onSelectItem}) => {
	const {getChartCollection} = useFirebaseContext()
	const {store: {folders = [], user = {}}, setFolders} = useWorkspaceContext()
	useEffect(() => {
		getChartCollection(user.id)
		.then(charts=>{
			setFolders(charts)
		})
	}, [user.id])
	return (
		<Menu
			mode="inline"
			style={{ height: '100%' }}
		>
		{
			_.map(folders, (folder) => {
				if(folder.children) {
					return (
						<SubMenu
							key={folder.uuid || folder.id}
							title={
								<span>
									<Icon type="folder" />
									{folder.name}
								</span>
							}
						>
						{
							_.map(folder.children, (child, i)=>(
								<Menu.Item key={child.uuid || child.id} onClick={()=>onSelectItem(child.uuid || child.id, child.spec, child.name, child.uuid)}>{child.name}</Menu.Item>
							))
						}
						</SubMenu>
					)
				} else {
					return (
						<Menu.Item key={folder.uuid || folder.id} onClick={()=>onSelectItem(folder.uuid||folder.id, folder.spec, folder.name, folder.uuid)}>{folder.name}</Menu.Item>
					)
				}
			})
		}
		</Menu>
	)
})

const WorkspaceTabs = memo(({tabs, activeKey, onSwitchTab, onAddTab, onRemoveTab}) => {
	
	const onEdit = useCallback(
		(target, action) => {
			if(action === 'add') {
				onAddTab()
			}
			if(action === 'remove') {
				onRemoveTab(target)
			}
		},
		[onAddTab, onRemoveTab]
	) 

	return (
		<Tabs
			type="editable-card"
			activeKey={activeKey}
			onChange={onSwitchTab}
			onEdit={onEdit}
		>
			{
				tabs.map((tab)=>(
					<Tabs.TabPane tab={tab.name} key={String(tab.id)}>
						{ activeKey === tab.id && <Editor tab={tab}/> }
					</Tabs.TabPane>
				))
			}
		</Tabs>	
	)
})

const Workspace = memo(() => {
	const {
		store,
		switchTab,
		removeTab,
		addTab
	} = useWorkspaceContext()
	return (
		<Layout style={{ padding: '24px 0', background: '#fff' }}>
			<Sider width={300} style={{ background: '#fff' }}>
				<WorkspaceFolder onSelectItem={addTab}/>
			</Sider>
			<Content style={{ padding: '0 24px', minHeight: 280 }}>
				<WorkspaceTabs
					tabs={store.tabs}
					activeKey={store.activeTab}
					onSwitchTab={switchTab}
					onAddTab={addTab}
					onRemoveTab={removeTab}
				/>
			</Content>
		</Layout>
	)
})

export default Workspace