import _ from 'lodash'
import React, { useState, useCallback } from 'react'
import { Layout, Menu, Modal } from 'antd';
import VegaLite from 'react-vega-lite';


import emojiSpec from '../../data/samples/emoji-bar.json';
import ganttSpec from '../../data/samples/gantt.json';
import histoSpec from '../../data/samples/histogram.json';
import waterSpec from '../../data/samples/waterfall.json';
import scatrSpec from '../../data/samples/scatter.json';
import bubblSpec from '../../data/samples/bubble.json';
import candlSpec from '../../data/samples/candlestick.json';
import errorSpec from '../../data/samples/errorband.json';
import decadSpec from '../../data/samples/decade-line.json';
import trailSpec from '../../data/samples/trail.json';
import {withRouter} from 'react-router-dom'
import { useWorkspaceContext } from '../../store/workspace.js';
// import { navigate } from 'gatsby';

const SAMPLES = [
	{
		spec: emojiSpec,
		title: 'Emoji',
		category: 'bar'
	},
	{
		spec: ganttSpec,
		title: 'Gantt',
		category: 'bar'
	},
	{
		spec: histoSpec,
		title: 'Histogram',
		category: 'bar'
	},
	{
		spec: waterSpec,
		title: 'Waterfall',
		category: 'bar'
	},
	{
		spec: bubblSpec,
		title: 'Bubble',
		category: 'scatter'
	},
	{
		spec: scatrSpec,
		title: 'Scatter',
		category: 'scatter'
	},
	{
		spec: candlSpec,
		title: 'Candlestick',
		category: 'scatter'
	},
	{
		spec: errorSpec,
		title: 'Error band',
		category: 'line'
	},
	{
		spec: decadSpec,
		title: 'Split line',
		category: 'line'
	},
	{
		spec: trailSpec,
		title: 'Trail',
		category: 'line'
	}
]

const ShowcaseModal = withRouter(({item, visible, onCancel, history}) => {
	const workspace = useWorkspaceContext()
	const onOK = useCallback(
		() => {
			workspace.addTab(undefined, item.spec, item.title)
			history.push('/workspace')
			// navigate('/workspace', {state: {item}})
		},
		[item],
	)

	return (
		<Modal
			title={item.title}
			visible={visible}
			onOk={onOK}
			okText={'Show Spec'}
			onCancel={onCancel}
			width={'90%'}
		>
			<VegaLite 
				spec={item.spec}
				style={{
					maxWidth:'100%',
					background: '#ffe',
					overflow: 'auto',
					display: 'flex',
					justifyContent: 'center'
				}} 
				renderer={'svg'}/>
		</Modal>
	)
})

const ShowcaseItem = ({item}) => {
	const [showModal, toggleModal] = useState(false)
	return (
		<div className={'col-4 mb-4'}>
			<div className="card" style={{width: '100%'}}>
				<div className="card-img-top">
					<VegaLite 
						spec={item.spec}
						style={{
							height: 200, 
							overflow: 'hidden',
							background: '#ffe',
							display: 'flex',
							justifyContent: 'center',
							alignItems: 'center'
						}} 
						renderer={'canvas'}/>
				</div>
				<div className="card-body">
					<h5 className="card-title">{item.title}</h5>
					<p className="card-text">Some description</p>
					<button className="btn btn-primary" onClick={()=>toggleModal(true)}>Preview</button>
					<ShowcaseModal item={item} visible={showModal} onCancel={()=>toggleModal(false)}/>
				</div>
			</div>
		</div>
	)
}

const ShowcaseGrid = ({items}) => {
	return (
		<div className={'row'}>
			{
				_.map(items, (item)=>(<ShowcaseItem key={item.title} item={item}/>))
			}
		</div>
	)
}

const Showcase = ({}) => {
	const [activeCategory, setCategory] = useState('bar')
	return (
		<Layout style={{ padding: '24px 0', background: '#fff', height: '100vh' }}>
			<Layout.Sider width={200}>
				<Menu
					mode="inline"
					style={{ height: '100%' }}
					defaultSelectedKeys={[activeCategory]}
				>
					{
						_.uniqBy(SAMPLES, 'category')
						.map((item)=>(<Menu.Item key={item.category} onClick={()=>setCategory(item.category)}>{_.startCase(item.category)}</Menu.Item>))
					}
				</Menu>
			</Layout.Sider>
			<Layout.Content style={{ padding: '0 24px', minHeight: 280 }}>
				<ShowcaseGrid items={_.filter(SAMPLES, {category: activeCategory})}/>
			</Layout.Content>
		</Layout>
	)
}

export default Showcase