import _ from 'lodash'
import React, { memo, useCallback } from 'react'
import VegaLite from 'react-vega-lite'
import { Tabs } from 'antd';
import { Handler } from 'vega-tooltip'
import Code from '../../../controls/code';
import { useWorkspaceContext } from '../../../../store/workspace';

const ExportTab = ({name, children}) => {
	return (
		<div className='pl-4' style={{overflow: 'auto', height: '100%'}}>
			<h5>{name}</h5>
			<div>{children}</div>
		</div>
	)
}

const ExportReact = ({spec}) => {
	const { data, ...rest } = spec
	return (
		<ExportTab name={'React Component'}>
			<p>Use the following code as your React component</p>
			<Code format={'jsx'}>
				{
`
import React from 'react';
import VegaLite from 'react-vega-lite';

const DATA = ${JSON.stringify(data, null, 2)};
const SPEC = ${JSON.stringify(rest, null, 2)};

const MyChart = () => {
  return (
    <VegaLite spec={SPEC} data={DATA}/>
  )
};

export default MyChart;
					`	
				}
			</Code>
		</ExportTab>
	)
}

const ExportJS = ({spec}) => {
	return (
		<ExportTab name={'JavaScript'}>
			<p><strong>Step 1:</strong> Import this scripts in your HTML head</p>
			<Code format="html" height={100}>
				{
					`
<script src="https://cdn.jsdelivr.net/npm/vega@5.3.5"></script>
<script src="https://cdn.jsdelivr.net/npm/vega-lite@3.2.1"></script>
<script src="https://cdn.jsdelivr.net/npm/vega-embed@4.0.0"></script>
					`
				}
			</Code>
			<p><strong>Step 2:</strong> Create a container (ie. <code>div</code>) in your HTML body with a unique ID.</p>
			<Code format="html" height={100}>
				{`<div id="containerID"></div>`}
			</Code>
			<p><strong>Step 3:</strong> Insert the following script at the end of your HTML body.</p>
			<Code format="html">
				{
					`
<script type="text/javascript">
  var SPEC = ${JSON.stringify(spec, null, 2)};
  vegaEmbed('#containerID', SPEC);
</script>
					`
				}
			</Code>
		</ExportTab>
	)
}

const ExportEmbed = () => {
	const { getActiveTab } = useWorkspaceContext()
	return (
		<ExportTab name={'Embedded'}>
			<p>Insert the following code into your HTML body:</p>
			<Code format="html" height={100}>
			{
				`
<iframe class="highcharts-iframe" src="/show/${getActiveTab().id}" style="border: 0; width: 100%; height: 500px"></iframe>
				`
			}
			</Code>
			Result:
			<iframe class="highcharts-iframe" src={`/show/${getActiveTab().id}`} style={{border: 0, width: '100%', height: 500}}></iframe>
		</ExportTab>
	)
}

const ExportAngular = ({spec}) => {
	return (
		<ExportTab name={'Angular Component'}>
			<p>To be defined</p>
		</ExportTab>
	)
}

const EXPORT_OPTIONS = [{
	name: 'React',
	icon: 'fab fa-react',
	Component: ExportReact
},{
	name: 'Angular',
	icon: 'fab fa-angular',
	Component: ExportAngular
},{
	name: 'JavaScript',
	icon: 'fab fa-js',
	Component: ExportJS
},{
	name: 'Embedded',
	icon: 'fas fa-link',
	Component: ExportEmbed
}]


const Export = ({spec}) => {
	return (
		<Tabs defaultActiveKey={EXPORT_OPTIONS[0].name}>
			{
				_.map(EXPORT_OPTIONS, (option)=>(
					<Tabs.TabPane style={{height: 500, overflow: 'auto'}} key={option.name} tab={<span><i className={option.icon}></i>{`  ${option.name}`}</span>}>
						<option.Component spec={spec}/>
					</Tabs.TabPane>
				))
			}
		</Tabs>
	)
}

const Chart = memo(({spec, onNewView}) => {
	console.log('chart is called')
	return (
		<div style={{boxShadow: '0px -1px 10px lightgrey', marginLeft: 24, boxSizing: 'border-box'}}>
			<Tabs tabPosition={'right'} defaultActiveKey={'1'} style={{height: 500}}>
				<Tabs.TabPane tab={'Chart'} key={'1'}>
					{
						!_.isEmpty(spec) &&
						<VegaLite spec={spec} data={spec.data} renderer={'svg'} onNewView={onNewView} tooltip={new Handler().call}/>
					}
				</Tabs.TabPane>
				<Tabs.TabPane tab={'Vega Spec'} key={'2'} >
					<Code format={'json'} style={{height: 500, overflow: 'auto'}}>{JSON.stringify(spec, null, 2)}</Code>
				</Tabs.TabPane>
				<Tabs.TabPane tab={'Share/Export'} key={'3'}>
					<Export spec={spec}/>
				</Tabs.TabPane>
			</Tabs>
		</div>
	)
})

export default Chart