import React, { useEffect, useState, useCallback } from 'react'
import { Button, Modal, Radio, Input } from 'antd';
import { useWorkspaceContext } from '../../store/workspace';
import { useFirebaseContext } from '../../services/firebase/context';

const SignInForm = ({error, name, setName}) => {
	return (
		<div>
			Username:
			<Input value={name} onChange={(e)=>setName(e.target.value)}/>
			{error}
		</div>
	)
}

const SignIn = () => {
	const { store : { user = {} }, setUser } = useWorkspaceContext()
	const { getUserById, getUserByName, createUser } = useFirebaseContext()
	const [ showModal, toggleModal ] = useState(false)
	
	const [name, setName] = useState('')
	const [error, setError] = useState('')

	useEffect(() => {
		if(user.id) {
			getUserById(user.id)
			.then(setUser)
			.catch(()=>toggleModal(true))
		} else {
			toggleModal(true)
		}
	}, [])

	const signIn = useCallback(
		() => {
			getUserByName(name)
			.then(user => {
				if(!user) {
					setError('User not found. Sign Up instead?')
				} else {
					setUser(user)
					toggleModal(false)
				}
			})
			.catch(e => {
				console.log(e)
				setError(e.message)
			})
		},
		[name, getUserByName, setUser],
	)

	const signUp = useCallback(
		() => {
			if(!name.length) {
				setError('Please write your name.')
				return
			}
			getUserByName(name)
			.then(user => {
				if(user) {
					setError('User exist. Please use another name or sign in.')
				} else {
					return createUser(name).then(user=> {
						setUser(user)
						toggleModal(false)
					})
				}
			})
			.catch(e => {
				setError(e.message)
			})
		},
		[name, getUserByName, setUser, createUser],
	)

	return (
		<div>
			<Modal
				title="Sign In / Sign Up"
				visible={showModal}
				okText={'Sign In'}
				onOk={signIn}
				onCancel={signUp}
				cancelText={'Sign Up'}
			>
				<SignInForm error={error} name={name} setName={setName}/>
			</Modal>
		</div>
	)
}

export default SignIn