import _ from 'lodash'
import React from 'react';
import { Layout as ALayout, Menu } from 'antd';
import 'antd/dist/antd.css';
import 'bootstrap/dist/css/bootstrap.min.css'
import '@fortawesome/fontawesome-free/css/all.css'
// import Auth from '../auth';
import { Link } from 'react-router-dom';
import SignIn from './sign-in';
import { useWorkspaceContext } from '../../store/workspace';

const { Header, Content, Footer } = ALayout;

const MENU_ITEMS = [{
	path: '/workspace',
	title: 'Workspace'
}, {
	path: '/showcase',
	title: 'Showcase'
}, {
	path: '/store',
	title: 'Store'
}, {
	path: '/docs',
	title: 'Documentation'
}, {
	path: '/support',
	title: 'Support'
}]

const Layout = ({location, children}) => {
	const { store : { user = {} } } = useWorkspaceContext()
	return (
		<ALayout>
			<SignIn/>
			<Header className="header">
				<Menu
					theme="dark"
					mode="horizontal"
					selectedKeys={[location && location.pathname]}
					style={{ lineHeight: '64px' }}
				>
					<Menu.Item>Welcome {_.capitalize(user.name)}!</Menu.Item>
					{
						MENU_ITEMS.map((item)=> (
							<Menu.Item key={item.path}><Link to={item.path}>{item.title}</Link></Menu.Item>
						))
					}
				</Menu>
			</Header>
			<Content>
				{ children }
			</Content>
			<Footer style={{ textAlign: 'center' }}>Chartman ©2019 Created by KNSE</Footer>
		</ALayout>
	)
} 

export default Layout