import _ from 'lodash'
import React, { useCallback, useEffect } from 'react'
import { Layout, Row, Col, Skeleton } from 'antd';
import DataPicker from '../../controls/data-picker';
import ControlGroup from './control-group';
import Chart from './chart';
import { useWorkspaceContext } from '../../../store/workspace';
import { useFirebaseContext } from '../../../services/firebase/context';


const Editor = () => {
	const store = useWorkspaceContext()
	const tab = store.getActiveTab()
	const { getBase } = useFirebaseContext()
	const shouldFetch = _.isEmpty(tab.spec) && tab.id
	useEffect(() => {
		if (shouldFetch) {
			getBase('charts', tab.id)
			.then(({spec, data}) => {
				store.changeSpec('', spec)
				store.setData(data.type, data.value)
			})
		}
	}, [tab.id])
	if (shouldFetch) {
		return <Skeleton active/>
	}
	return (
		<div>
			<DataPicker tab={tab}/>
			<Row>
				<Col span={12}>
					<ControlGroup tab={tab} onChange={store.changeSpec} store={store.store}/>
				</Col>
				<Col span={12}>
					<Chart spec={tab.spec} view={store.store.view} onNewView={store.setView}/>
				</Col>
			</Row>
		</div>
	)
}

export default Editor