import React, { useEffect, useState } from 'react'
import { Modal, Tabs, Button, Skeleton, Form, Input } from 'antd';
import { Link } from 'react-router-dom'
import VegaLite from 'react-vega-lite/lib/VegaLite';
import { getDefaultSpec } from '../../data/helpers';
import bar from '../../data/samples/defaults/bar.json';
import scatter from '../../data/samples/defaults/scatter.json';
import line from '../../data/samples/defaults/line.json';
import { useFirebaseContext } from '../../services/firebase/context';
import { useWorkspaceContext } from '../../store/workspace';
import { formItemLayout } from '../../helpers/form';
import { useHistory } from '../../helpers/router';

const { TabPane } = Tabs;

const DEFAULT_CHARTS = [
	{
		title: 'Simple bar',
		description: 'A bar chart encodes quantitative values as the extent of rectangular bars',
		spec: bar
	},
	{
		title: 'Scatter plot',
		description: 'A scatter plot with url data',
		spec: scatter
	},
	{
		title: 'Line graph',
		description: 'A time-series data of stock',
		spec: line
	},

]

const WelcomeChart = ({
	chartSelected
}) => {
	const { saveBase, getBase } = useFirebaseContext();
	const { addTab } = useWorkspaceContext();
	const [ charts, setCharts ] = useState([]);
	useEffect(() => {
		// saveBase('settings', 'default_charts', {
		// 	name: 'Default Charts',
		// 	value: DEFAULT_CHARTS
		// })
		getBase('settings', 'default_charts')
		.then(result => {
			if(result) {
				setCharts(result.value);
			}
		})
	}, [])
	const onChartSelected = (chart) => {
		addTab(undefined, chart.spec)
		chartSelected()
	}
	return (
		<>
			<h6>Select one of the default chart specifications:</h6>
			<div className={'row'}>
				{
					!charts.length &&
					<Skeleton active/>
				}
				{
					charts.map((chart)=>(
						<div key={chart.title} className={'col-4 mb-4'}>
							<div className="card" style={{width: '100%'}}>
								<div className="card-img-top">
									<VegaLite
										spec={chart.spec}
										style={{
											height: 200, 
											overflow: 'hidden',
											background: '#ffe',
											display: 'flex',
											justifyContent: 'center',
											alignItems: 'center'
										}} 
										renderer={'canvas'}/>
								</div>
								<div className="card-body">
									<h5 className="card-title">{chart.title}</h5>
									<p className="card-text">{chart.description}</p>
									<Button type="primary" size="small" onClick={()=>onChartSelected(chart)}>+ Start</Button>
								</div>
							</div>
						</div>
					))
				}
				
			</div>
		</>
	)
}

const WelcomeCollection = () => {
	let desc = 'A collection is a group of charts you want to manage together in an organized way.'
	desc += ' It can be for a research paper, a project you are part of or for your team to share and collaborate.'
	const { store } = useWorkspaceContext()
	const { createBase } = useFirebaseContext()
	const defaultCollection = { name: '', description: '', user_id: store.user.id }
	const [ collection, setCollection ] = useState(defaultCollection)
	const onCreateCollection = () => {
		createBase('collection', collection)
	}
	return (
		<>
			<h6>{desc}</h6>
			<Form>
				<Form.Item label={'Name'}>
					<Input value={collection.name} onChange={(v)=>setCollection({...collection, name: v.target.value})}/>
				</Form.Item>
				<Form.Item label={'Description'}>
					<Input.TextArea value={collection.description} onChange={(v)=>setCollection({...collection, description: v.target.value})}/>
				</Form.Item>
			</Form>
			<Button type="primary" onClick={onCreateCollection}>+ Create Collection</Button>
		</>
	)
}

const Welcome = () => {
	const { store } = useWorkspaceContext()
	const [visible, toggleVisibility] = useState(!store.tabs.length)
	const history = useHistory()
	return (
		<Modal
			title={ !!store.user.name ? `Welcome back, ${store.user.name}!` : 'Welcome to Chartman!'}
			visible={visible}
			width={'80%'}
			footer={null}
			>
			<div style={{height: 500, overflowY: 'auto'}}>
				<h3>What would you like to do?</h3>
				<Tabs defaultActiveKey="1">
					<TabPane tab="Create Chart" key="1">
						<WelcomeChart chartSelected={()=>{
							toggleVisibility(false); 
							history.push('/workspace')
							}}/>
					</TabPane>
					<TabPane tab="Create Collection" key="2">
						<WelcomeCollection/>
					</TabPane>
					<TabPane tab="Explore Showcase" key="3">
						<Link to="/showcase" onClick={()=>toggleVisibility(false)}>Click here to go to the showcase page.</Link>
					</TabPane>
				</Tabs>
			</div>
		</Modal>
	)
}

export default Welcome