import React, {createContext, useContext, useEffect, useState} from 'react'
import * as firebase from './index'

const FirebaseContext = createContext(firebase);

const FirebaseProvider = ({children}) => {
	useEffect(() => {
		firebase.initialize()
	}, [])
	return (
		<FirebaseContext.Provider value={firebase}>
			{children}
		</FirebaseContext.Provider>
	)
}

const useFirebaseContext = () => {
	return useContext(FirebaseContext)
}

const useFirebaseSnapshot = (collection, initialValue, userId) => {
	const [ value, setValue ] = useState(initialValue)
	const { snapshotBase } = useFirebaseContext()
	useEffect(()=>{
		snapshotBase(collection, userId, (res) => {
			setValue(res)
		})
	}, [collection, userId])
	return value
}

export {
	FirebaseContext,
	useFirebaseContext,
	useFirebaseSnapshot,
	FirebaseProvider
}