import _ from 'lodash'
import React, { useCallback } from 'react'
import { findSchema } from '../../../data/helpers';
import { Select, Form } from 'antd';

const Repeat = ({
	value: { 
		row = [], 
		column = [] 
	} = {}, 
	config: {
		options = []
	} = {}, 
	onChange = () => {}
}) => {

	const onRowChange = useCallback(
		(value) => {
			onChange({
				column, row: value
			})
		},
		[onChange, column],
	)
	const onColumnChange = useCallback(
		(value) => {
			onChange({
				row, column: value
			})
		},
		[onChange, row],
	)

	return (
		<div>
			<p>
				Repeat allows multi-variable and complex data to be split into multiple charts represented in column/rows of charts.
			</p>
			<p>
				An example of the usage of repeat is <a href="https://www.originlab.com/doc/Origin-Help/Trellis-Plot">Trellis plot</a>.
			</p>
			<div>
			<Form.Item
				label="Row"
        >
				<Select
					mode="multiple"
					style={{ width: '100%' }}
					placeholder="Please select"
					defaultValue={row}
					onChange={onRowChange}
				>
					{
						options.map((option)=><Select.Option key={option}>{option}</Select.Option>)
					}
				</Select>
			</Form.Item>
			<Form.Item
				label="Column"
        >
				<Select
					mode="multiple"
					style={{ width: '100%' }}
					placeholder="Please select"
					defaultValue={column}
					onChange={onColumnChange}
				>
					{
						options.map((option)=><Select.Option key={option}>{option}</Select.Option>)
					}
				</Select>
			</Form.Item>
			</div>
		</div>
	)
}

const RepeatSchema = {
	title: 'Repeat',
	id: 'repeat',
	path: 'repeat',
	generator: (row = [""], column = [""]) => {
		return { row, column }
	},
	Component: Repeat,
	config: (store) => {
		return {
			options: _.chain(store)
				.get('view._runtime.data.data_0.values.value[0]')
				.keys()
				.value()
		}
	},
	$schema: findSchema('RepeatRef')
}

export default Repeat
export {
	RepeatSchema
}